import Vue from 'vue'
import Router from 'vue-router'
import Cmsindex from '@/components/cmsindex'
import Login from '@/components/login'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '*', redirect: '/login'},
    {
      path: '/Hompage',
      name: 'Cmsindex',
      component: Cmsindex
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    }
  ]
})



