<template>
  <div>
    <div class="content" ref="content">
      <div class="width-25">
        <h1>添加数据</h1>
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="图片名称" prop="url">
            <el-input v-model="ruleForm.url"></el-input>
          </el-form-item>
          <!-- =new Date() | dateformat('YYYY-MM-DD HH:mm:ss') -->
          <el-form-item label="时间" prop="datetime">{{ruleForm.datetime}}</el-form-item>
          <el-form-item label="选择时间">
            <el-date-picker
              v-model="ruleForm.datetime"
              type="datetime"
              placeholder="选择日期时间"
              value-format="yyyy-MM-dd HH:mm:ss"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="活动配文" prop="txt">
            <el-input type="textarea" v-model="ruleForm.txt"></el-input>
          </el-form-item>
          <el-form-item label="归属栏目" prop="type">
            <el-select v-model="ruleForm.type" placeholder="请选择图片所属栏目" @change="getdata">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">添加上传</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>

      <!-- 编辑弹出框 -->
      <el-dialog title="提示" :visible.sync="centerDialogVisible" width="30%" center>
        <el-form
          :model="editForm"
          status-icon
          :rules="rules"
          ref="editForm"
          label-width="80px"
          class="demo-ruleForm" 
          style="overflow: hidden;"
        >
          <el-form-item label="图片名称" prop="url">
            <el-input v-model="editForm.url" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="时间" prop="datetime">{{editForm.datetime}}</el-form-item>
          <el-form-item label="活动配文" prop="txt">
            <el-input v-model.number="editForm.txt"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitEdit('editForm')">提交修改</el-button>
            <el-button @click="resetFormEdit('editForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

      <div class="width-75">
        <!-- <el-popover ref="popover5" placement="top" width="160" v-model="visible2">
          <p>这是一段内容这是一段内容确定删除吗？</p>
          <div style="text-align: right; margin: 0">
            <el-button size="mini" type="text" @click="visible2 = false">取消</el-button>
            <el-button type="primary" size="mini" @click="visible2 = false">确定</el-button>
          </div>
        </el-popover>-->

        <h1>
          <span
            style="color:#f10202"
          >{{ruleForm.type==1?'“三生”主题馆':ruleForm.type==2?'京津冀资源展':ruleForm.type==3?'玩转科学':ruleForm.type==4?'科学秀场':ruleForm.type==5?'“幕后工作者”':ruleForm.type==6?'首都科技创新成果展':ruleForm.type==7?'科教专区':ruleForm.type==8?'中国科协和北京市领导视察活动现场':ruleForm.type==9?'待定1':ruleForm.type==10?'待定2':ruleForm.type==11?'待定3':ruleForm.type==0?'测试':''}}</span> 数据列表
        </h1>
        <div style="width: 100%;overflow-y: scroll;" :style="{height:ht +'px'}">
          <el-table
            :data="tableData3"
            border
            style="width: 100%;"
            :row-class-name="tableRowClassName"
          >
            <el-table-column prop="id" label="ID" width="180" sortable></el-table-column>
            <el-table-column prop="datetime" label="日期" width="180" sortable></el-table-column>
            <el-table-column prop="txt" label="活动配文" width="180"></el-table-column>
            <el-table-column prop="url" label="地址">
              <template slot-scope="scope">
                <el-popover trigger="hover" placement="top">
                  <img :src="scope.row.url" alt width="400px;" />
                  <div slot="reference" class="name-wrapper">
                    <i class="el-icon-picture-outline"></i>
                    <el-tag size="medium">{{ scope.row.url }}</el-tag>
                  </div>
                </el-popover>
              </template>
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="100">
              <template slot-scope="scope">
                <el-button @click="handleClickdel(scope.row)" type="text" size="small">删除</el-button>
                <el-button type="text" size="small" @click="handleClick(scope.row)">编辑</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div style="text-align: center;display:none">
            <div style="display: inline-block;float:initial;">
              <el-pagination layout="prev, pager, next" :total="(tableData3.length/11*10)"></el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const axios = require("axios");
let moment = require("moment");
import qs from "qs";
// moment(new Date()).format("YYYY-MM-DD HH:mm:ss")
//url 为线上接口请求地址
let url = "http://yun.neweasco.com:3001";
let url2 = "http://127.0.0.1:3001";
export default {
  data() {
    return {
      centerDialogVisible: false,
      ht: 0,
      timer: "",
      ruleForm: {
        datetime: moment(new Date()).format("YYYY-MM-DD HH:mm:ss"),
        txt: "",
        url: "",
        type: 1
      },
      editForm: {
        datetime: moment(new Date()).format("YYYY-MM-DD HH:mm:ss"),
        id: "",
        likes: "",
        txt: "",
        url: ""
      },
      options: [
        { value: 1, label: "“三生”主题馆" },
        { value: 2, label: "京津冀资源展" },
        { value: 3, label: "玩转科学" },
        { value: 4, label: "科学秀场" },
        { value: 6, label: "首都科技创新成果展" },
        { value: 5, label: "“幕后工作者”" },
        { value: 7, label: "科教专区" },
        { value: 8, label: "中国科协和北京市领导视察活动现场" },
        { value: 9, label: "待定1" },
        { value: 10, label: "待定2" },
        { value: 11, label: "待定3" },
        { value: 0, label: "测试项目" },
      ],
      rules: {
        url: [{ required: true, message: "请输入图片名称", trigger: "blur" }],
        txt: [{ required: true, message: "请填写活动配图", trigger: "blur" }],
        type: [{ required: true, message: "请选择所属栏目", trigger: "blur" }]
      },
      tableData3: []
    };
  },
  watch: {},
  mounted: function() {
    this.reload(1);
    this.$nextTick(() => {
      console.log(document.documentElement.clientHeight);
      this.ht =
        document.documentElement.clientHeight -
        document.documentElement.clientHeight / 4;
    });
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer); // 在Vue实例销毁前，清除我们的定时器
    }
  },
  methods: {
    getdata(val) {
      //change 获取不同的列表
      this.reload(val);
    },
    reload(type) {
      let data = { type: type };
      axios
        .post(url + "/api/getAll", qs.stringify(data))
        .then(response => {
          // handle success
          this.tableData3 = response.data.msg;
        })
        .catch(function(error) {
          // handle error
          console.log(error);
        })
        .finally(function() {
          // always executed
        });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "success-row";
      }
      return "";
    },
    submitForm(formName) {
      //非空校验
      let verify = true;
      this.$refs[formName].validate(valid => {
        if (!valid) {
          this.$message({
            message: "请将数据填写完整再提交",
            type: "error"
          });
          verify = false;
        }
      });
      if (!verify) {
        return;
      }

      let data = {
        txt: this.ruleForm.txt,
        datetime: this.ruleForm.datetime,
        type: this.ruleForm.type
      };
      let imgurl = "";
      if (data.type == 1) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_1/";
      } else if (data.type == 2) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_2/";
      } else if (data.type == 3) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_3/";
      } else if (data.type == 4) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_4/";
      } else if (data.type == 5) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_5/";
      } else if (data.type == 6) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_6/";
      } else if (data.type == 7) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_7/";
      } else if (data.type == 8) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_8/";
      } else if (data.type == 9) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_9/";
      } else if (data.type == 10) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_10/";
      } else if (data.type == 11) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_11/";
      } else if (data.type == 0) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_test/";
      }
      data.url = imgurl + this.ruleForm.url + ".jpg";
      console.log(data);
      axios
        .post(url + `/api/adddata`, qs.stringify(data), {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
          }
        })
        .then(res => {
          this.$message({
            message: "恭喜你，成功了",
            type: "success"
          });
          this.reload(data.type);
        })
        .catch(error => {
          this.$message({
            message: "失败了",
            type: "error"
          });
        });
    },
    submitEdit(formName) {
      //非空校验
      let verify = true;
      this.$refs[formName].validate(valid => {
        if (!valid) {
          this.$message({
            message: "请将数据填写完整再提交",
            type: "error"
          });
          verify = false;
        }
      });
      if (!verify) {
        return;
      }
      let data = {
        id:this.editForm.id,
        txt: this.editForm.txt,
        datetime: this.editForm.datetime,
        type: this.ruleForm.type,
        likes: this.editForm.likes,
      };
      let imgurl = "";
      if (data.type == 1) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_1/";
      } else if (data.type == 2) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_2/";
      } else if (data.type == 3) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_3/";
      } else if (data.type == 4) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_4/";
      } else if (data.type == 5) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_5/";
      } else if (data.type == 6) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_6/";
      } else if (data.type == 7) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_7/";
      } else if (data.type == 8) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_8/";
      } else if (data.type == 9) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_9/";
      } else if (data.type == 10) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_10/";
      } else if (data.type == 11) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_11/";
      } else if (data.type == 0) {
        imgurl = "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_test/";
      }
      data.url = imgurl + this.editForm.url + ".jpg";
      console.log(data);
      axios
        .post(url + `/api/updateEdit`, qs.stringify(data), {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
          }
        })
        .then(res => {
          this.$message({
            message: "恭喜你，成功了",
            type: "success"
          });
          this.reload(data.type);
          this.centerDialogVisible=false;
        })
        .catch(error => {
          this.$message({
            message: "失败了",
            type: "error"
          });
        });


    },
    resetForm(formName) {
      //重置
      this.ruleForm.datetime = moment(new Date()).format("YYYY-MM-DD HH:mm:ss");
      this.ruleForm.txt = "";
      this.ruleForm.url = "";
    },
    resetFormEdit() {
      //重置编辑
      this.editForm.txt = "";
      this.editForm.url = "";
    },
    handleClick(row) {
      row.type = this.ruleForm.type;
      axios
        .post(url + `/api/edit`, qs.stringify(row), {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
          }
        })
        .then(res => {
          this.editForm = res.data.msg[0];
          let imgurl = "";
          if (row.type == 1) {
            imgurl =
              "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_1/";
          } else if (row.type == 2) {
            imgurl =
              "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_2/";
          } else if (row.type == 3) {
            imgurl =
              "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_3/";
          } else if (row.type == 4) {
            imgurl =
              "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_4/";
          } else if (row.type == 5) {
            imgurl =
              "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_5/";
          } else if (row.type == 6) {
            imgurl =
              "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_6/";
          } else if (row.type == 7) {
            imgurl =
              "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_7/";
          } else if (row.type == 8) {
            imgurl =
              "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_8/";
          } else if (row.type == 9) {
            imgurl =
              "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_9/";
          } else if (row.type == 10) {
            imgurl =
              "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_10/";
          } else if (row.type == 11) {
            imgurl =
              "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_11/";
          } else if (row.type == 0) {
            imgurl =
              "http://yun.neweasco.com:88/photo/A/DCIM/100EOS5D/photo_test/";
          }

          //转化图片地址
          this.editForm.url=this.editForm.url.replace(imgurl,"");
          this.editForm.url=this.editForm.url.replace('.jpg',"");
          this.centerDialogVisible = true;

          console.log(this.editForm)
        })
        .catch(error => {
          this.$message({
            message: "失败了",
            type: "error"
          });
        });
    },
    handleClickdel(row) {
      row.type = this.ruleForm.type;
      axios
        .post(url + `/api/del`, qs.stringify(row), {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
          }
        })
        .then(res => {
          this.$message({
            message: "恭喜你，删除成功",
            type: "success"
          });
          this.reload(row.type);
        })
        .catch(error => {
          this.$message({
            message: "失败了",
            type: "error"
          });
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.content {
  clear: both;
  overflow-y: auto;
  overflow-x: hidden;
}
.content div {
  float: left;
}

.width-25 {
  width: 25%;
}
.width-75 {
  width: 73%;
  margin-left: 0%;
  margin-right: 2%;
}
</style>
<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}

.demo-ruleForm>.el-form-item{
  width: 100%;
}

</style>



